import { PromiseDebouncer } from '../../libs/PromiseDebouncer';

/* eslint-disable import/prefer-default-export */

/**
 * An notifications feed service that provides data.
 */
export class NotificationService {
    constructor(args) {
        if (!args) {
            throw new Error(
                `args is required when instantiating a new NotificationService`
            );
        }

        [`notificationAdapter`].forEach((name) => {
            if (!args[name]) {
                throw new Error(
                    `${name} property is required when instantiating a new NotificationService`
                );
            }
        });

        this.promiseDebouncer = new PromiseDebouncer();
        this.notificationAdapter = args.notificationAdapter;
    }

    /**
     * Get a list of notifications.
     * @param {object} args The notifications dto.
     * @returns {Promise<array>} A promised list of notifications.
     */
    getNotifications(args) {
        return this.promiseDebouncer.debounce(
            {
                method: `getNotifications`,
                ...args,
            },
            () => this.notificationAdapter.getNotifications(args)
        );
    }

    /**
     * Get a list of pinned notifications.
     * @param {object} args The notifications dto.
     * @returns {Promise<array>} A promised list of notifications.
     */
    getPinnedNotifications(args) {
        return this.promiseDebouncer.debounce(
            {
                method: `gePinnedNotifications`,
                ...args,
            },
            () => this.notificationAdapter.getPinnedNotifications(args)
        );
    }

    /**
     * Get a count of unread notifications.
     * @param {object} args The notifications dto.
     * @returns {Promise<integer>} A count of the number of unread.
     */
    getUnreadNotificationsCount(args) {
        return this.promiseDebouncer.debounce(
            {
                method: `getUnreadNotificationsCount`,
                ...args,
            },
            () => this.notificationAdapter.getUnreadNotificationsCount(args)
        );
    }

    /**
     *  Update the read status on the notification
     * @param {object} args The notifications dto.
     * @returns {Promise} The changed notification
     */
    setRead(args) {
        return this.notificationAdapter.setRead(args);
    }

    /**
     *  Update the read pinned on the notification
     * @param {object} args The notifications dto.
     * @returns {Promise} The changed notification
     */
    setPinned(args) {
        return this.notificationAdapter.setPinned(args);
    }
}
