import React, { createContext, useState, useCallback } from 'react';

/**
 * The context.
 * @export
 */
export const NotificationContext = createContext({
    isLoading: false,
    notifications: [],
    getNotifications: null,
    isMoreNotifications: false,

    setRead: null,
    unreadNotifications: 0,
    getUnreadNotificationsCount: null,

    // Optional
    pinnedNotifications: [],
    getPinnedNotifications: null,
    setPinned: null,
});

/**
 * The context provider.
 * @param {object} props The provider props.
 * @returns {JSX.Element} Context provider component.
 */
export const NotificationProvider = ({ children, notificationService }) => {
    const [isLoading, setIsLoading] = useState(false);
    const [pinnedNotifications, setPinnedNotifications] = useState([]);
    const [notifications, setNotifications] = useState([]);
    const [isMoreNotifications, setIsMoreNotifications] = useState(false);
    const [unreadNotificationsCount, setUnreadNotificationsCount] = useState(0);

    // Function to get count of unread notifications
    const getUnreadNotificationsCount = useCallback((args) => {
        setIsLoading(true);
        return notificationService
            .getUnreadNotificationsCount(args)
            .then((count) => {
                setUnreadNotificationsCount(count);
                setIsLoading(false);
            });
    }, []);

    // Function to get pinned notifications
    const getPinnedNotifications = useCallback((args) => {
        setIsLoading(true);
        return notificationService
            .getPinnedNotifications(args)
            .then((results) => {
                setPinnedNotifications(results);
                setIsLoading(false);
            });
    }, []);

    // Function to get notifications or get the next page of notifications
    const getNotifications = useCallback(
        (args) => {
            setIsLoading(true);
            return notificationService.getNotifications(args).then((result) => {
                // If there is no start position its a fresh request
                if (!args.start || args.reload) {
                    setNotifications(result.notifications);
                } else {
                    // Otherwise its an additional page so add it to the internal data
                    setNotifications(
                        [...notifications].concat(result.notifications)
                    );
                }

                setIsMoreNotifications(result.next);
                setIsLoading(false);
            });
        },
        [notifications]
    );

    // Set the read flag on a notification
    const setRead = (args) => {
        setIsLoading(true);
        return notificationService.setRead(args).then((result) => {
            // Update the local copy of the notifications using the result return
            const updatedNotifications = [...notifications];

            // Find the item to update (from response) in the local list
            const index = notifications.findIndex(
                (notification) => notification.id === result.id
            );
            if (index !== -1) {
                updatedNotifications[index] = result;
                setNotifications(updatedNotifications);

                // If successfuly changed to read, decrement the unread counter
                if (result.read) {
                    setUnreadNotificationsCount(unreadNotificationsCount - 1);
                }
            }
            setIsLoading(false);
        });
    };

    /*
        Pin the notification: alert the notification service than it has been pinned / unpinned
        then move the item locally between the pinned and regular list. This may require
        a data reload, see below.
    */
    const setPinned = (args) => {
        setIsLoading(true);
        return notificationService.setPinned(args).then((result) => {
            // Update the local copy of the notifications using the result return
            let updatedNotifications = [...notifications];
            let updatedPinnedNotifications = [...pinnedNotifications];

            // Move the notification from one list to the other
            if (args.pinned) {
                // Being pinned so remove from regular list
                updatedNotifications = notifications.filter(
                    (notification) => notification.id !== result.id
                );
                // Add into pinned list
                updatedPinnedNotifications.push(result);
                // Sort it into the correct place in the list (newest shown first)
                updatedPinnedNotifications.sort((a, b) => {
                    return b.createdDate.getTime() - a.createdDate.getTime();
                });

                // Update notifications list to remove the now pinned notification
                setNotifications(updatedNotifications);
            } else {
                // Being unpinned so remove from pinned list
                updatedPinnedNotifications = pinnedNotifications.filter(
                    (notification) => notification.id !== result.id
                );

                /*
                    To keep the paginated loading in sync the notifications list may needs to be 
                    refreshed if the notification being removed doesnt slot in above the last loaded
                    notification as otherwise its unknown its place in the order.

                    If the unpinned notification is newer (higher getTime number) than the last item
                    in the notifications list it can just be dropped into the list; if its older or
                    equal a reload is needed to be sure where it should be in the pageing.
                */
                const lastNotification = updatedNotifications.slice(-1).pop();
                if (
                    !lastNotification ||
                    result.createdDate.getTime() <
                        lastNotification.createdDate.getTime()
                ) {
                    // Reload needed to check where the notification should be
                    getNotifications({
                        reload: true,
                        start: 0,
                        max: notifications.length,
                    });
                } else {
                    // No reload needed, add into notifications list
                    updatedNotifications.push(result);
                    // Sort it into the correct place in the list (newest shown first)
                    updatedNotifications.sort((a, b) => {
                        return (
                            b.createdDate.getTime() - a.createdDate.getTime()
                        );
                    });
                    // Update notifications list
                    setNotifications(updatedNotifications);
                }
            }

            // Add or remove from the pinned notifications area
            setPinnedNotifications(updatedPinnedNotifications);
            setIsLoading(false);
        });
    };

    return (
        <NotificationContext.Provider
            value={{
                isLoading,
                notifications,
                getNotifications,
                isMoreNotifications,

                setRead,
                unreadNotificationsCount,
                getUnreadNotificationsCount,

                pinnedNotifications,
                getPinnedNotifications,
                setPinned,
            }}
        >
            {children}
        </NotificationContext.Provider>
    );
};
